<template>
  <div id="bar-service-number-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        tooltip: {
          trigger: "item",
        },
        color: ["#F64837", "#BA963F", "#13C665"],
        legend: {
          data: ["开道", "快反", "交警", "警车", "巡警"],
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "10%",
            top: "50",
            right: "0%",
            bottom: "20",
          },
        ],
        xAxis: {
          type: "category",
          data: ["1", "4", "8", "12", "16", "20", "24"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "开道",
            symbol: "none",
            data: [0, 2, 3, 5, 15, 20, 23],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#F64837",
                },
              },
            },
          },
          {
            name: "快反",
            symbol: "none",
            data: [0, 5, 10, 15, 28, 10, 53],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#BA963F",
                },
              },
            },
          },
          {
            name: "交警",
            symbol: "none",
            data: [0, 2, 3, 4, 5, 7, 12],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#13C665",
                },
              },
            },
          },
          {
            name: "警车",
            symbol: "none",
            data: [0, 3, 13, 15, 22, 33, 36],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#4B7DEA",
                },
              },
            },
          },
          {
            name: "巡警",
            symbol: "none",
            data: [0, 1, 2, 2, 4, 6, 13],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#5B523F",
                },
              },
            },
          },
        ],
      },
      numberServiceTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.numberServiceTimer = setInterval(() => {
      this.asyncReportedCar();
      this.initEchart();
    }, 3000);
  },
  computed: {
    ...mapState("userService", ["reportedCar"]),
  },
  methods: {
    ...mapActions("userService", ["asyncReportedCar"]),

    initEchart() {
      const optionsBar = {
        tooltip: {
          trigger: "item",
        },
        color: ["#F64837", "#BA963F", "#13C665", "#4B7DEA", "#5B523F"],
        legend: {
          data: ["开道", "交警", "快反", "巡警", "警车"],
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "8%",
            top: "50",
            right: "0%",
            bottom: "20",
          },
        ],
        xAxis: {
          type: "category",
          data: ["1", "4", "8", "12", "16", "20", "24"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "开道",
            symbol: "none",
            data: [0, 2, 3, 5, 15, 20, this.reportedCar.makeDetour],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#F64837",
                },
              },
            },
          },
          {
            name: "交警",
            symbol: "none",
            data: [0, 5, 10, 15, 28, 10, this.reportedCar.trafficPolice],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#BA963F",
                },
              },
            },
          },
          {
            name: "快反",
            symbol: "none",
            data: [0, 2, 3, 4, 5, 7, this.reportedCar.fastReflexion],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#13C665",
                },
              },
            },
          },
          {
            name: "巡警",
            symbol: "none",
            data: [0, 3, 13, 15, 22, 33, this.reportedCar.patrolPolice],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#4B7DEA",
                },
              },
            },
          },
          {
            name: "警车",
            symbol: "none",
            data: [0, 1, 2, 2, 4, 6, this.reportedCar.policeCar],
            type: "line",
            smooth: true,
            itemStyle: {
              normal: {
                lineStyle: {
                  color: "#5B523F",
                },
              },
            },
          },
        ],
      };
      this.optionsBar = optionsBar;
      const barServiceTypeChart = document.querySelector(
        "#bar-service-number-charts"
      );
      if (!barServiceTypeChart) {
        clearInterval(this.numberServiceTimer);
        return;
      }
      const myChart = echarts.init(barServiceTypeChart);
      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#bar-service-number-charts {
  width: 100%;
  height: 195px;
}
</style>