<template>
  <div id="line-trend-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      // 总警力类型数据
      optionsBar: {
        tooltip: {
          trigger: "item",
        },
        legend: {
          data: ["警情"],
          itemStyle: {
            color: "#FFBD2E",
          },
          lineStyle: {
            color: "#FFBD2E",
          },
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "13%",
            top: "50",
            right: "13%",
            bottom: "22",
          },
        ],
        xAxis: {
          type: "category",
          data: ["00:00", "04:00", "08:00", "12:00", "16:00", "20:00", "24:00"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "警情",
            symbol: "none",
            data: [0, 1, 1, 1, 2, 3, 4, 0],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(16,138,238,0.20)",
            },
          },
        ],
      },
      // 设置trend定时器
      timerTrend: null,
    };
  },
  mounted() {
    this.initEchart();
    this.timerTrend = setInterval(() => {
      this.initEchart();
    }, 3000);
  },
  computed: {
    ...mapState("userLeader", ["acceptWarning"]),
  },
  methods: {
    initEchart() {
      const optionsBar = {
        tooltip: {
          trigger: "item",
        },
        legend: {
          data: ["警情"],
          itemStyle: {
            color: "#FFBD2E",
          },
          lineStyle: {
            color: "#FFBD2E",
          },
          textStyle: {
            color: "#fff",
          },
          left: "right",
        },
        grid: [
          {
            left: "13%",
            top: "50",
            right: "10%",
            bottom: "22",
          },
        ],
        xAxis: {
          type: "category",
          data: ["00:00", "04:00", "08:00", "12:00", "16:00", "20:00", "24:00"],
          axisLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)",
            },
          },
          axisLabel: {
            color: "#fff",
            interval: 0,
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255,255,255,0.1)"],
              width: 1,
              type: "solid",
            },
          },
        },
        series: [
          {
            name: "警情",
            symbol: "none",
            data: [0, 1, 1, 1, 1, 0, 1, this.acceptWarning],
            type: "line",
            smooth: true,
            areaStyle: {
              color: "rgba(16,138,238,0.20)",
            },
          },
        ],
      };
      this.optionsBar = optionsBar;
      const lineTrendChart = document.querySelector("#line-trend-charts");
      if (!lineTrendChart) {
        clearInterval(this.timerTrend);
        return;
      }
      const myChart = echarts.init(lineTrendChart);

      myChart.setOption(this.optionsBar);
    },
  },
};
</script>

<style lang="scss" scoped>
#line-trend-charts {
  width: 100%;
  height: 210px;
}
</style>