<template>
  <div id="pie-type-leader-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["22%", "66%"],
            center: ["50%", "50%"],
            data: [
              { value: 3, name: "一级 1个" },
              { value: 5, name: "二级 2个" },
              { value: 10, name: "三级 459个" },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              normal: {
                color: function (colors) {
                  var colorList = ["#5389FF", "#1B3B81", "#3567D4"];
                  return colorList[colors.dataIndex];
                },
              },
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      },
    };
  },
  computed: {
    ...mapState("userLeader", ["acceptWarning"]),
  },
  mounted() {
    this.initEchart();
  },
  watch: {
    acceptWarning: function (value, oldValue) {
      this.initEchart();
    },
  },
  methods: {
    initEchart() {
      const optionsPie = {
        backgroundColor: "transparent",
        // tooltip: {
        //   trigger: "item",
        // },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["33%", "75%"],
            center: ["50%", "50%"],
            // radius: [50, 250],
            data: [
              { value: 1, name: `一级 1` },
              { value: 2, name: "二级 2" },
              { value: 3, name: `三级 ${this.acceptWarning}` },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "area",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              normal: {
                color: function (colors) {
                  var colorList = ["#5389FF", "#1B3B81", "#3567D4"];
                  return colorList[colors.dataIndex];
                },
              },
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      };
      this.optionsPie = optionsPie;
      const pieTypeChart = document.querySelector("#pie-type-leader-charts");
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-type-leader-charts {
  width: 100%;
  height: 230px;
}
</style>