<template>
  <div id="pie-service-type-charts"></div>
</template>

<script>
import * as echarts from "echarts";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      // 总警力类型数据
      optionsPie: {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        visualMap: {
          show: false,
          min: 80,
          max: 600,
          inRange: {
            color: ["#5389ff", "#1b3b331"],
          },
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["18%", "70%"],
            center: ["55%", "50%"],
            data: [
              { value: 42, name: "警车 42" },
              { value: 63, name: "巡警 63" },
              { value: 25, name: "快反 25" },
              { value: 61, name: "交警 61" },
              { value: 42, name: "开道 42" },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              color: "#c23531",
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)",
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      },
      // 设置trend定时器
      trendServiceTimer: null,
    };
  },
  mounted() {
    this.initEchart();
    this.trendServiceTimer = setInterval(() => {
      this.asyncReportedCar();
      this.initEchart();
    }, 3000);
  },
  computed: {
    ...mapState("userService", ["reportedCar"]),
  },
  methods: {
    ...mapActions("userService", ["asyncReportedCar"]),
    initEchart() {
      const optionsPie = {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
        },
        series: [
          {
            name: "总警力量类型数量",
            type: "pie",
            radius: ["18%", "70%"],
            center: ["51%", "50%"],
            data: [
              {
                value: this.reportedCar.makeDetour,
                name: `警车 ${this.reportedCar.makeDetour}`,
              },
              {
                value: this.reportedCar.trafficPolice,
                name: `巡警 ${this.reportedCar.trafficPolice}`,
              },
              {
                value: this.reportedCar.fastReflexion,
                name: `快反 ${this.reportedCar.fastReflexion}`,
              },
              {
                value: this.reportedCar.patrolPolice,
                name: `交警 ${this.reportedCar.patrolPolice}`,
              },
              {
                value: this.reportedCar.policeCar,
                name: `开道 ${this.reportedCar.policeCar}`,
              },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 0.3)",
            },
            labelLine: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)",
              },
              smooth: 0.2,
              length: 10,
              length2: 20,
            },
            itemStyle: {
              // color: "#c23531",
              // shadowBlur: 200,
              // shadowColor: "rgba(0, 0, 0, 0.5)",
              normal: {
                color: function (colors) {
                  var colorList = [
                    "#5389FF",
                    "#1B3B81",
                    "#3567D4",
                    "#4277EB",
                    "#1F479F",
                  ];
                  return colorList[colors.dataIndex];
                },
              },
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function (idx) {
              return Math.random() * 200;
            },
          },
        ],
      };
      this.optionsPie = optionsPie;
      const pieTypeChart = document.querySelector("#pie-service-type-charts");
      if (!pieTypeChart) {
        clearInterval(this.trendServiceTimer);
        return;
      }
      const myChart = echarts.init(pieTypeChart);
      myChart.setOption(this.optionsPie);
    },
  },
};
</script>

<style lang="scss" scoped>
#pie-service-type-charts {
  width: 100%;
  height: 230px;
}
</style>